// __tests__/components/EmotionSelector.test.tsx

import React from 'react';
import { render, fireEvent } from '@testing-library/react-native';
import EmotionSelector from '../../src/components/EmotionSelector';

describe('EmotionSelector 컴포넌트', () => {
  const mockEmotions = [
    { emotion_id: 1, name: '행복', icon: 'emoticon-happy-outline', color: '#FFD700' },
    { emotion_id: 2, name: '감사', icon: 'hand-heart', color: '#FF69B4' },
  ];
  
  const mockOnSelectEmotion = jest.fn();
  
  beforeEach(() => {
    jest.clearAllMocks();
  });

  it('감정 선택기가 올바르게 렌더링되어야 함', () => {
    const { getByText } = render(
      <EmotionSelector
        emotions={mockEmotions}
        selectedEmotions={[]}
        onSelectEmotion={mockOnSelectEmotion}
      />
    );
    
    expect(getByText('행복')).toBeDefined();
    expect(getByText('감사')).toBeDefined();
  });

  it('감정 아이템을 클릭하면 onSelectEmotion이 호출되어야 함', () => {
    const { getByText } = render(
      <EmotionSelector
        emotions={mockEmotions}
        selectedEmotions={[]}
        onSelectEmotion={mockOnSelectEmotion}
      />
    );
    
    fireEvent.press(getByText('행복'));
    
    expect(mockOnSelectEmotion).toHaveBeenCalledWith(mockEmotions[0]);
  });

  it('이미 선택된 감정이 강조되어야 함', () => {
    const { getByTestId } = render(
      <EmotionSelector
        emotions={mockEmotions}
        selectedEmotions={[mockEmotions[0]]}
        onSelectEmotion={mockOnSelectEmotion}
      />
    );
    
    // 선택된 감정 아이템에 testID 속성이 있다고 가정합니다.
    // EmotionSelector 컴포넌트에서 이런 형식으로 testID를 설정해야 합니다.
    const selectedItem = getByTestId(`emotion-item-${mockEmotions[0].emotion_id}`);
    expect(selectedItem).toBeDefined();
  });
});